@import url(https://fonts.googleapis.com/css2?family=Pacifico&family=Roboto:wght@100&family=Yanone+Kaffeesatz:wght@500&display=swap);
@import url(https://fonts.googleapis.com/css2?family=Montserrat&family=Pacifico&family=Roboto:wght@100;400&family=Source+Sans+Pro&family=Yanone+Kaffeesatz:wght@500&display=swap);
@import url(https://fonts.googleapis.com/css2?family=Pacifico&family=Roboto:wght@100;400&family=Yanone+Kaffeesatz:wght@500&display=swap);
@import url(https://fonts.googleapis.com/css2?family=Pacifico&family=Roboto:wght@100;400&family=Yanone+Kaffeesatz:wght@500&display=swap);

.App {
  text-align: center;
}


.fontType{
    font-family: 'Yanone Kaffeesatz', sans-serif;
    font-size: 25px;
}

.tittleFontType{
    font-family: 'Pacifico', cursive;
    font-size: 20px;  
    bottom: 10px;
}
.cartImage{
    margin-right: 20px;
}
.ItemDetailStyle{
    display: flex;
    width: 100%;
    flex-direction: row;
}

.ItemDetailInfo{
    margin: 0px auto;
    display: flex;
    flex-direction: column;
}

.ItemDetailName{
    font-size: 60px !important; 
    font-family: 'Source Sans Pro', sans-serif !important;
    font-style: italic;
    font-weight: 600 !important;
    display: flex;
    justify-content: center;
    align-items: center;
}

.ItemDetailPrice{
    position: relative;
    top: 20px;
    font-size: 60px !important;
    font-family: 'Source Sans Pro', sans-serif !important;
    display: flex;
    justify-content: center;
}






.counter{
    display: flex;
    flex-direction: row;
   justify-content: center;
   justify-content: space-around;
}
.imageCart{
    width: 25%;
}

.widthTable{
    width: 20%;
}
.itemListContainer{
    display: flex;
    flex-wrap: wrap;
    grid-gap: 20px;
    gap: 20px;
}
.cardsContainer{
    display: flex;
    flex-wrap: wrap;
    grid-gap: 20px;
    gap: 20px;
}
.letterPrice{
    font-size: 18px;
    font-family: 'Roboto', sans-serif;
    color: #000000;
}

.letterProduct{
    font-size: 20px;
    font-family: 'Pacifico', cursive;
    color: black;
    font-weight: 400;
}

.buttonShop{
    margin: auto;
}
